}
.h3-whitepaper {
  padding: 0px 18px 18px 18px;
  color: #001c3e;
  font-size: 36px;
}
.h2-header {
  font-size: 46px;
  padding: 40px;
  color: #0d5595;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}
.container-whitepaper {
  padding: 20px;
  margin-bottom: 20px;
}
.container1 {
  background-color: #d4aea5ad;
  border-radius: 50px;
}
.container2 {
  background-color: #757388b0;
  border-radius: 50px;
}
.container3 {
  background-color: rgb(19 106 128 / 55%);
