}
.container4 {
  background-color: #58777cb0;
  border-radius: 50px;
}
.container5 {
  background-color: #eaeaea9e;
  border-radius: 50px;
}
.container6 {
  background-color: #c27fa2c2;
  border-radius: 50px;
}

.prg-whitepaper {
  color: #001c3e;
  font-size: 24px;
}
.h2-footer {
  font-size: 46px;
  padding: 40px;
  color: #1b889c;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 2);
}
